<template>
  <div class="dynamic">
    <p><router-link to="/"></router-link> / </p>
    <div class="banner">
        <img src="https://ly809652226.github.io/imageserver/banner2.jpg">
    </div>
    <div class="main">
      <div class="left">
          <h4 class="title"></h4>
          <div class="content">
             <ul>
               <li v-for="item in dynamics">
                 <a href="#">{{item.title}}</a>
                 <span>
                    <i class="iconfont icon-ai-eye"></i> {{item.num}}
                    <i class="iconfont icon-shijianzhongbiao2"></i> {{item.time}}
                 </span>
                 <p>{{item.describe}}</p>
               </li>
             </ul>
          </div>
      </div>
      <Right></Right>
    </div>
  </div>
</template>
<script>
// 
import Right from '../common/ClassContact'

export default {
  name: "dynamic",
  data(){
    return {
       dynamics: [
          {
             title: '',
             describe: ' 1 2 3 4...',
             num: '102',
             time: '2016-09-22'
          },
          {
             title: '',
             describe: '1214147 ...',
             num: '101',
             time: '2016-09-22'
          },
          {
             title: ' ',
             describe: '11...',
             num: '101',
             time: '2016-09-22'
          },
          {
             title: '5 ',
             describe: '  1213...',
             num: '102',
             time: '2016-09-22'
          },
          {
             title: '',
             describe: '126 ()100  ...',
             num: '103',
             time: '2016-09-22'
          }
       ]
    }
  },
  components: {
    Right
  }
}
</script>
<style lang="less" scoped>
.dynamic {
    width: 100%;
    margin-top: 10px;
    p {
        width: 100%;
        font-size: 14px;
        color: #808080;
        a {
           color: #555;
        }
    }
    .banner {
        margin-top: 15px;
        img {
           max-width: 100%;
           width: 100%;
        }
    }
    .main {
        width: 100%;
        height: 600px;
        margin-top: 15px;
        border-radius: 5px;
        .left {
           width: 79%;
           height: 600px;
           float: left;
           h4 {
             font-size: 16px;
           }
           .content {
               width: 99.8%;
               height: 553px;
               li {
                  height: 80px;
                  padding: 15px;
                  overflow: hidden;
                  border-bottom: 1px solid #ddd;
                  &:hover {
                     background: #F5F5F5;
                  }
                  a {
                     color: #333;
                     font-weight: bold;
                     &:hover {color: #0D3D88}
                  }
                  p {
                     margin-top: 10px;
                     line-height: 1.5;
                     margin-bottom: 10px;
                  }
                  span {
                     font-size: 13px;
                     color: #757575;
                     font-weight: normal;
                     float:right;
                     i {
                        font-size: 13px;
                        margin-left: 10px;
                     }
                  }
               }
           }
        }
    }
}
</style>



