<template>
  <div class="synopsis">
     <p><router-link to="/"></router-link> / </p>
     <Banner></Banner>
     <div class="article">
         <header>
            <h1></h1>
         </header>
         <section>
              <p>&nbsp; &nbsp; &nbsp; &nbsp; 2014+</p><br>
              <p>&nbsp; &nbsp; &nbsp; &nbsp;&nbsp;12349</p><br>
         </section>
         <div class="footer">
             <span></span>,
         </div>
     </div>
  </div>
</template>
<script>
// Banner
import Banner from '../common/Banner'

export default {
  name: "synopsis",
  data(){
     return {}
  },
  components: {
    Banner
  }
}
</script>
<style lang="less" scoped>
.synopsis {
  width: 100%;
  margin-top: 10px;
  p {
    width: 100%;
    font-size: 14px;
    color: #808080;
    a {
       color: #555;
    }
  }
  .article {
     width: 100%;
     margin-top: 15px;
     border: 1px solid #ddd;
     border-radius: 5px;
     header {
        padding: 10px 20px;
        h1 {
          margin-bottom: 0;
          margin-top: 10px;
          font-size: 18px;
          line-height: 1.5;
        }
     }
     section {
        padding: 10px 20px;
        font-size: 14px;
        line-height: 2;
        p {color: #1f2d3d}
     }
     .footer {
         width: 100%;
         height: 80px;
         border-top: 1px dotted #ccc;
         font-size: 14px;
         line-height: 80px;
         span {
            font-weight: bold;
            color: #666;
            margin-left: 50px;
         }
     }
  }
}
</style>



